import { $axios } from "@/service/app-http";

const GET_BORN_ENPOINT = "born/get-born-data-separeted";
const UPDATE_COMPLICATION_ENDPOINT = "born/update-complications";
const UPDATE_OOCYTE_COLLECTION_ENDPOINT = "born/update-oocyte-collections";
const UPDATE_OBSTETRICAL_HISTORY_ENDPOINT = "born/update-obstetrical-history";

export async function getBorn({ patientId, cycleId }) {
    const { data } = await $axios.get(GET_BORN_ENPOINT, {
        params: { patientId, cycleId }
    });
    return data;
}

export async function updateComplication(payload) {
    const { data } = await $axios.post(UPDATE_COMPLICATION_ENDPOINT, payload);
    return data;
}

export async function updateOocyteCollection(payload) {
    const { data } = await $axios.post(
        UPDATE_OOCYTE_COLLECTION_ENDPOINT,
        payload
    );
    return data;
}

export async function updateObstetricalHistory(payload) {
    const { data } = await $axios.post(
        UPDATE_OBSTETRICAL_HISTORY_ENDPOINT,
        payload
    );
    return data;
}
