import * as utilService from "@/service/utils";

const state = () => ({
    bornOptions: null
});

const actions = {
    async getBornOptions({ state }) {
        try {
            const data = await utilService.getBornOptions();
            state.bornOptions = data;
        } catch (err) {
            state.bornOptions = null;
        }
    }
};

